.header{
	position: relative;
	flex: 0 1 450px;
	@include m-transition($property: all, $duration: 0.3s, $timing_function: ease-in-out, $delay: initial);
}
.header-wrapper{
	background-color: $blue;
	position: fixed;
	top: 0; 
	left: 0;
	width: 450px;
	height: 100%;
	@include m-transition($property: all, $duration: 0.3s, $timing_function: ease-in-out, $delay: initial);
}

.header-btn{
	top: 0;
	left: -50px;
	width: 50px;
	height: 50px;
	background-color: $blue;
	background-size: cover;
	background-repeat: no-repeat;
	position: fixed;
	cursor: pointer;
	@include m-transition($property: all, $duration: 0.3s, $timing_function: ease-in-out, $delay: initial);
	z-index: 300;
}
.header-btn.mod-nav{
	background-image: url('../assets/icons/menu.png');
}
.header-btn.mod-close{
	background-image: url('../assets/icons/close.png');
}

@include tablet{
	.header{
		flex: 0;
		z-index: 200;
	}
	.header-wrapper{
		left: -450px;
		width: 450px;
	}
	.header-wrapper.mod-visible{
		left: 0;
	}
	.header-btn.mod-nav{
		left: 0;
	}
	.header-btn.mod-nav.mod-invisible{
		left: -50px;
	}
	.header-btn.mod-close{
		left: 0;
	}
	.header-btn.mod-close.mod-invisible{
		left: -50px;
	}
}
@include mobile{
	.header{
		flex: 0;
		z-index: 200;
	}
	.header-wrapper{
		left: -100%;
		height: 100%;
		width: 100%;
	}
	.header-wrapper.mod-visible{
		left: 0;
	}
	.header-btn.mod-nav{
		left: 0;
	}
	.header-btn.mod-nav.mod-invisible{
		left: -50px;
	}
	.header-btn.mod-close{
		left: 0;
	}
	.header-btn.mod-close.mod-invisible{
		left: -50px;
	}
}