.list{
	position: relative;
}
.list.mod-text_left{
	text-align: left;
}
.list.mod-padding_bottom{
	padding-bottom: 50px;
}
.list.mod-indented{
	width: 700px;
	padding: 25px 25px;	
	padding-right: 0;
}
.list.mod-padding_top_null{
	padding-top: 0;
}
.list.mod-footer{
	width: 700px;
	display: flex;
	justify-content: space-around;
	padding-bottom: 50px;
}
.list.mod-footer:last-child{
	padding-bottom: 0;
}
.list-item{
	font-size: 1.15rem;
	line-height: 1.75rem;
	letter-spacing: 0.05em;
	text-align: left;
	max-width: 700px;
	flex: 0 0 1;
	font-weight: 400;
	position: relative;
}
.list-item.mod-nav{
	margin-bottom: 10vh;
	max-width: 300px;
	/*border-bottom: 3px solid $transparent;*/
	/*@include m-transition();*/
}
.list-item.mod-nav:hover{
	/*border-bottom-color: $link_nav;*/
}
.list-item.mod-download{
	padding-bottom: 50px;
}
.list-item.mod-checked{
	padding-left: 2rem;
	line-height: 2.5rem;
	color: $blue;
	font-weight: 600;
}
.list-item.mod-checked:before{
	content: '✓';	
	background-size: contain;
	background-repeat: no-repeat;
	position: absolute;
	height: 3rem;
	width: 3rem;
	font-size: 2rem;
	left: 0;
}
.list-item.mod-dash{
	padding-left: 0.75rem;
	line-height: 2.5rem;
	font-weight: 600;
	color: $blue;
}
.list-item.mod-dash.mod-black{
	color: $black;
}
.list-item.mod-dash:before{
	content: '-';
	background-size: contain;
	background-repeat: no-repeat;
	position: absolute;
	font-size: 1.25rem;
	line-height: 1.75rem;
	left: 0;
	top: 0.25rem;
}
.list-item.mod-sidebar{
	float: left;
	font-size: 0.75rem;
	line-height: 1rem;
	padding-right: 25px;
}
.list-item.mod-sidebar:last-child{
	padding-right: 0;
}

@include ie{
	.list-item.mod-dash:before{
		top: 18px;
	}
}

@include mobile{
	.list-item{
		line-height: 1.25rem;
		font-size: 1rem;
	}
	.list-item.mod-checked{
		line-height: 1.5rem;
		padding-bottom: 15px;
	}
	.list-item.mod-dash{
		padding-bottom: 15px;
	}
	.list.mod-indented{
		width: auto;
		padding: 25px 25px;	
		padding-right: 0;
	}
	.list.mod-footer{
		width: auto;
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		padding-bottom: 50px;
	}
	.list.mod-footer .list-item{
		padding-bottom: 25px;
	}
}