.h1{
	font-size: 5rem;
	font-weight: 600;
}
.h2{
	font-size: 3.25rem;
	line-height: 4.25rem;
	font-weight: 400;
	padding-bottom: 50px;
}
.h3{
	font-size: 1.5rem;
	line-height: 2.75rem;
	padding-bottom: 20px;
	font-weight: 600;
	letter-spacing: 0.1px;
}
.h3.mod-red{
	color: $red;
	padding-bottom: 0;
}
.h4{
	font-size: 1.5rem;
	line-height: 2.75rem;
	font-weight: 600;
}
.h4.mod-padding_bottom{
	padding-bottom: 25px;
}
.h5{
	font-size: 1.25rem;
	line-height: 2rem;
	font-weight: 600;
	padding-bottom: 50px;
}

@include tablet{
	.h2{
		font-size: 2.75rem;
		line-height: 4.25rem;
		font-weight: 400;
		padding-bottom: 50px;
	}
	.h3{
		font-size: 1.25rem;
		line-height: 2.75rem;
		padding-bottom: 20px;
		font-weight: 600;
		letter-spacing: 0.1px;
	}
}

@include mobile{
	.h2{
		font-size: 2.25rem;
		line-height: 4.25rem;
		font-weight: 400;
		padding-bottom: 50px;
	}
	.h3{
		font-size: 1.5rem;
		line-height: 2.75rem;
		padding-bottom: 20px;
		font-weight: 600;
		letter-spacing: 0.1px;
	}
	.h4{
		font-size: 1.5rem;
		line-height: 2.75rem;
		font-weight: 600;
	}
	.h5{
		font-size: 1.25rem;
		line-height: 2rem;
		font-weight: 600;
		padding-bottom: 50px;
	}
}