.nav{
	padding: 15vh 50px;
	padding-bottom: 0;
	position: relative;
}
.nav-logo{
	height: 100%;
	position: absolute;
	right: 0;
	top: 0;
	width: 90px;
}
.nav-logo-element{
	top: 0;
	left: 0;
	position: absolute;
	@include m-transition($property: all, $duration: 0.75s, $timing_function: ease-in-out, $delay: initial);
}
.nav-logo-element.mod-zug{
	z-index: 100;
	/*background-color: $blue;*/
	background-image: url('../assets/logo/zug_klein.png');
	background-size: cover;
	background-repeat: no-repeat;
	width: 90px;
	height: 51px;
}
.nav-logo-element.mod-kabine{
	top: -85vh;
	background-image: url('../assets/logo/kabine_klein.png');	
	background-size: cover;
	background-repeat: no-repeat;
	background-position: bottom;
	width: 90px;
	height: 100vh;
}
.nav-logo-element.mod-gewicht{
	top: -15vh;
	background-image: url('../assets/logo/gewicht_klein.png');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: bottom;
	width: 90px;
	height: 100vh;
}



@include ie{
	.nav-logo{		
		/*display: none;*/
	}
}

@include mobile{
	.nav-logo{
		display: none;
	}
}