#aufzugwaerter{
	/*background-image: url('../assets/images/aufzug01.jpg');
	background-size: cover;
	background-position: bottom;*/
	background-color: $white;
	padding: 0;
	padding-bottom: 100px;
}
.home-hero{
	width: 100%;
	height: 100vh;
	background-image: url('../assets/images/hero.jpg');
	background-size: cover;
	background-position: center;
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	justify-content: center;
	margin-bottom: 100px;
}
.home-content{
	padding: 0 50px;
}
.home-h1{
	font-size: 3rem;
	padding: 50px;	
	max-width: 600px;
	margin-bottom: 50px;
	/*line-height: 3.25rem;*/
	text-transform: uppercase;
	letter-spacing: 0.05em;
	position: relative;
}
.home-h1:after{
	content: '';
	background-image: url('../assets/logo/logo.png');
	background-size: 90px;
	background-repeat: no-repeat;
	background-position: top;
	overflow-y: hidden;
	width: cover;
	height: 1000px;
	top: -90px;
	right: -50px;
	position: absolute;
	width: 90px;
}
.home-h2{
	font-size: 1.75rem;
	line-height: 3rem;
	padding-bottom: 50px;
	font-weight: 800;
	text-transform: uppercase;
	font-style: italic;
	letter-spacing: 0.1px;
}
.home-h3{
	font-size: 1.75rem;
	line-height: 2rem;
	padding-bottom: 50px;
	font-weight: 600;
}
.home-h4{
	font-size: 1.5rem;
	line-height: 4rem;
	padding-bottom: 50px;
	font-weight: 600;
}
.home-h5{
	font-size: 1.15rem;
	line-height: 2rem;
	/*padding-top: 1.25rem;*/
	font-weight: 800;
	color: $red;
	max-width: 700px;
	/*padding: 25px;*/
	text-transform: uppercase;
}
.home-p{
	font-size: 1.25rem;
	line-height: 1.75rem;
	letter-spacing: 0.05em;
	font-weight: 600;
	/*max-width: 85%;*/
	padding-bottom: 50px;
	/*padding: 25px;*/
}
.home-p.mod-no_padding{
	padding: 0;
}
.home-address{
	padding-bottom: 50px;
}

@include ie{
	.home-h1{
		padding-bottom: 0;
	}
}


@include mobile{
	.home-hero{
		margin-bottom: 25px;
	}
	.home-content{
		padding: 0 25px;
	}
	.home-h1{
		font-size: 2rem;
		padding: 25px;
		padding-right: 60px;	
		max-width: 600px;
		margin-bottom: 0px;
		/*line-height: 3.25rem;*/
		text-transform: uppercase;
		letter-spacing: 0.05em;
		position: relative;
	}
	.home-h1:after{
		content: '';
		background-image: url('../assets/logo/logo.png');
		background-size: 50px;
		background-repeat: no-repeat;
		background-position: top;
		overflow-y: hidden;
		width: cover;
		height: 1000px;
		top: -50px;
		right: 5px;
		position: absolute;
		width: 50px;
	}
	.home-h2{
		padding: 25px;
		padding-top: 0;
		padding-bottom: 25px;
		font-size: 1.25rem;
		line-height: 1.75rem;
	}
	.home-h3{
	font-size: 1.5rem;
	line-height: 2rem;
	padding-left: 25px;
	padding-right: 25px;
	padding-bottom: 50px;
	font-weight: 600;
	}
	.home-h4{
		font-size: 1rem;
		line-height: 2rem;
		padding-bottom: 50px;
		font-weight: 600;
	}
	.home-p{
		margin-bottom: 25px;
	}
}