.text{
	font-size: 1.15rem;
	line-height: 1.75rem;
	padding-bottom: 25px;
	letter-spacing: 0.05em;
	text-align: left;
	width: 700px;
	flex: 0 0 1;
	font-weight: 400;
}
.text.mod-padding_big{
	padding-bottom: 50px;
}
.text.mod-padding_null{
	padding: 0;
}
.text.mod-center{
	text-align: center;
}

@include mobile{
	.text{
		width: 100%;
		font-size: 1rem;
	}
}