.link{
	text-decoration: none;
	position: relative;
	color: $link;
	border-bottom: 2px solid $transparent;
	/*white-space: nowrap;*/
	@include m-transition();
}
.link:hover{
	border-bottom-color: $link;
}
.link.mod-nav{
	color: $link_nav;
	text-transform: uppercase;
	font-size: 1.5rem;
	line-height: 2rem;
	/*font-weight: bold;*/
}
.link.mod-nav:hover{
	/*border-bottom-color: $link_nav;*/
	/*border-bottom-width: 3px;*/
	border-bottom-color: $transparent;
}
.link.mod-logo{
	font-size: 2.15rem;
	line-height: 3rem;
	font-weight: normal;
	display: inline-block;
	max-width: 300px;
}
.link.mod-icon{
	margin-left: 4rem;
}
.link.mod-icon:before{
	content: '';	
	background-size: contain;
	background-repeat: no-repeat;
	position: absolute;
	height: 3rem;
	width: 3rem;
	top: .25rem;
	left: -4rem;
}
.link.mod-icon .email{
	text-decoration: none;
	position: relative;
	color: $link;
	border-bottom: 3px solid $transparent;
}
.link.mod-mail:before{
	background-image: url('../assets/icons/email.png');
}
.link.mod-phone:before{
	background-image: url('../assets/icons/phone.png');
	height: 4rem;
	width: 4rem;
	top: -.25rem;
	/*left: -0.45rem;*/
}
.link.mod-pdf:before{
	background-image: url('../assets/icons/pdf.png');
	top: -.75rem;
}
.link.mod-inline_block{
	font-weight: 600;
	font-size: 1.25rem;
	line-height: 1.75rem;
	padding-top: 1rem;
	/*padding-right: 25px;*/
	display: inline-block;
}
.link.mod-inline_block:last-child{
	padding-right: 0;
}
.link.mod-sidebar{
	color: $link_nav;
}
.link.mod-sidebar .email{
	text-decoration: none;
	position: relative;
	color: $link;
	border-bottom: 3px solid $transparent;
	@include m-transition();
	color: $link_nav;
}

@include mobile{
	.link.mod-mail{
		font-size: 1.15rem;
		display: block!important;
		margin-bottom: 25px;
	}
	.link.mod-phone{
		font-size: 1.15rem;
		display: block!important;
	}
	.link.mod-pdf{
		margin-left: 0;
		font-size: 1rem;
	}
	.link.mod-pdf:before{
		top: 0;
		height: 2rem;
		width: 2rem;
		left: -2.5rem;
	}
}